.register-container {
  display: flex;
  height: 100vh;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  overflow: hidden;
}

/* ÿßŸÑÿ¨ÿßŸÜÿ® ÿßŸÑÿ£Ÿäÿ≥ÿ± */
.register-left {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 2rem;
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(10px);
  text-align: center;
  color: white;
}

.truck-image {
  width: 200px;
  margin-bottom: 2rem;
  filter: drop-shadow(0 10px 20px rgba(0, 0, 0, 0.3));
}

.register-title {
  font-size: 2.5rem;
  font-weight: 800;
  margin-bottom: 1rem;
}

.register-subtitle {
  font-size: 1.1rem;
  opacity: 0.9;
  line-height: 1.6;
  max-width: 400px;
}

/* ÿßŸÑÿ¨ÿßŸÜÿ® ÿßŸÑÿ£ŸäŸÖŸÜ */
.register-right {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 2rem;
  background: white;
}

.register-form-container {
  width: 100%;
  max-width: 450px;
  background: white;
  padding: 2.5rem;
  border-radius: 20px;
  box-shadow: 0 15px 35px rgba(0, 0, 0, 0.1);
  border: 1px solid #e0e0e0;
}

.register-header {
  text-align: center;
  margin-bottom: 2rem;
}

.register-badge {
  display: inline-block;
  background: linear-gradient(45deg, #ff6b6b, #ee5a24);
  color: white;
  padding: 0.5rem 1rem;
  border-radius: 25px;
  font-size: 0.9rem;
  font-weight: 600;
  margin-bottom: 1rem;
}

.register-header h2 {
  font-size: 1.8rem;
  color: #2d3748;
  margin-bottom: 0.5rem;
  font-weight: 700;
}

.register-header p {
  color: #718096;
  font-size: 0.95rem;
}

/* ŸÜŸÖŸàÿ∞ÿ¨ ÿßŸÑÿ™ÿ≥ÿ¨ŸäŸÑ */
.register-form {
  display: flex;
  flex-direction: column;
  gap: 1.2rem;
}

.form-group {
  display: flex;
  flex-direction: column;
}

.form-group label {
  font-weight: 600;
  margin-bottom: 0.5rem;
  color: #4a5568;
  font-size: 0.95rem;
}

.form-group input,
.form-group select {
  padding: 1rem;
  border: 2px solid #e2e8f0;
  border-radius: 10px;
  font-size: 1rem;
  transition: all 0.3s ease;
  background: #f8fafc;
  font-family: inherit;
}

.form-group input:focus,
.form-group select:focus {
  outline: none;
  border-color: #667eea;
  background: white;
  box-shadow: 0 0 0 3px rgba(102, 126, 234, 0.1);
}

.form-group input::placeholder {
  color: #a0aec0;
}

/* ÿ≤ÿ± ÿßŸÑÿ™ÿ≥ÿ¨ŸäŸÑ */
.btn-primary {
  padding: 1rem;
  background: linear-gradient(45deg, #667eea, #764ba2);
  color: white;
  border: none;
  border-radius: 10px;
  font-size: 1.1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  margin-top: 0.5rem;
  font-family: inherit;
}

.btn-primary:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 20px rgba(102, 126, 234, 0.3);
}

.btn-primary:active {
  transform: translateY(0);
}

/* ÿ±ÿßÿ®ÿ∑ ÿ™ÿ≥ÿ¨ŸäŸÑ ÿßŸÑÿØÿÆŸàŸÑ */
.login-link {
  text-align: center;
  margin-top: 1.5rem;
  color: #718096;
  font-size: 0.95rem;
}

.login-link a {
  color: #667eea;
  font-weight: 600;
  text-decoration: none;
  transition: color 0.3s ease;
}

.login-link a:hover {
  color: #764ba2;
  text-decoration: underline;
}

/* ÿ±ÿ≥ÿßÿ¶ŸÑ ÿßŸÑÿ™ŸÜÿ®ŸäŸá */
.message {
  text-align: center;
  margin-top: 1rem;
  padding: 0.8rem;
  border-radius: 8px;
  font-weight: 500;
  font-size: 0.9rem;
}

.message:not(:empty) {
  background: #edf2f7;
  border: 1px solid #e2e8f0;
}

/* ÿ™ÿ£ÿ´Ÿäÿ±ÿßÿ™ ÿßŸÑÿ≠ÿ±ŸÉÿ© */
@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(30px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.register-form-container {
  animation: fadeInUp 0.6s ease-out;
}

/* ÿ™ÿ£ÿ´Ÿäÿ±ÿßÿ™ Responsive */
@media (max-width: 768px) {
  .register-container {
    flex-direction: column;
    height: auto;
  }
  
  .register-left {
    padding: 3rem 1rem;
  }
  
  .register-right {
    padding: 2rem 1rem;
  }
  
  .register-form-container {
    margin: 1rem;
  }
}

/* ÿ™ÿ≠ÿ≥ŸäŸÜÿßÿ™ ŸÑŸÑŸàÿ∂ÿπ verification */
.verification-mode .register-header h2 {
  color: #e53e3e;
}

.verification-mode .register-badge {
  background: linear-gradient(45deg, #e53e3e, #c53030);
}
/* ÿ™ÿ£ÿ´Ÿäÿ± ÿßŸáÿ™ÿ≤ÿßÿ≤ ÿßŸÑÿ¥ÿßÿ≠ŸÜÿ© ÿßŸÑŸàÿßŸÇÿπŸä */
@keyframes truckDriving {
  0% {
    transform: translateY(0px) rotate(0deg);
  }
  10% {
    transform: translateY(-2px) rotate(-0.5deg);
  }
  20% {
    transform: translateY(1px) rotate(0.3deg);
  }
  30% {
    transform: translateY(-3px) rotate(0.8deg);
  }
  40% {
    transform: translateY(2px) rotate(-0.7deg);
  }
  50% {
    transform: translateY(-1px) rotate(0.2deg);
  }
  60% {
    transform: translateY(-4px) rotate(-0.9deg);
  }
  70% {
    transform: translateY(3px) rotate(0.6deg);
  }
  80% {
    transform: translateY(-2px) rotate(-0.4deg);
  }
  90% {
    transform: translateY(1px) rotate(0.1deg);
  }
  100% {
    transform: translateY(0px) rotate(0deg);
  }
}

/* ÿ™ÿ£ÿ´Ÿäÿ± ÿßŸáÿ™ÿ≤ÿßÿ≤ ŸÇŸàŸä (ŸÑŸÑŸÖÿ∑ÿ®ÿßÿ™ ÿßŸÑŸÉÿ®Ÿäÿ±ÿ©) */
@keyframes truckBump {
  0% {
    transform: translateY(0px) rotate(0deg);
  }
  15% {
    transform: translateY(-8px) rotate(-2deg);
  }
  30% {
    transform: translateY(12px) rotate(3deg);
  }
  45% {
    transform: translateY(-6px) rotate(-1deg);
  }
  60% {
    transform: translateY(4px) rotate(1deg);
  }
  75% {
    transform: translateY(-2px) rotate(-0.5deg);
  }
  100% {
    transform: translateY(0px) rotate(0deg);
  }
}

/* ÿ≠ÿ±ŸÉÿ© ÿ™ŸÇÿØŸÖ ÿßŸÑÿ¥ÿßÿ≠ŸÜÿ© ŸÖÿπ ÿßŸáÿ™ÿ≤ÿßÿ≤ÿßÿ™ */
@keyframes truckMovingForward {
  0% {
    transform: translateX(-20px) translateY(0px) rotate(0deg);
  }
  10% {
    transform: translateX(-10px) translateY(-2px) rotate(-0.5deg);
  }
  20% {
    transform: translateX(0px) translateY(1px) rotate(0.3deg);
  }
  30% {
    transform: translateX(10px) translateY(-3px) rotate(0.8deg);
  }
  40% {
    transform: translateX(20px) translateY(2px) rotate(-0.7deg);
  }
  50% {
    transform: translateX(30px) translateY(-1px) rotate(0.2deg);
  }
  60% {
    transform: translateX(20px) translateY(-4px) rotate(-0.9deg);
  }
  70% {
    transform: translateX(10px) translateY(3px) rotate(0.6deg);
  }
  80% {
    transform: translateX(0px) translateY(-2px) rotate(-0.4deg);
  }
  90% {
    transform: translateX(-10px) translateY(1px) rotate(0.1deg);
  }
  100% {
    transform: translateX(-20px) translateY(0px) rotate(0deg);
  }
}

/* ÿ™ÿ≠ÿØŸäÿ´ ŸÉŸÑÿßÿ≥ ÿßŸÑÿ¥ÿßÿ≠ŸÜÿ© */
.truck-image {
  width: 200px;
  margin-bottom: 2rem;
  filter: drop-shadow(0 10px 20px rgba(0, 0, 0, 0.3));
  /* ÿ™ÿ∑ÿ®ŸäŸÇ ÿ™ÿ£ÿ´Ÿäÿ± ÿßŸÑŸÇŸäÿßÿØÿ© ÿßŸÑŸàÿßŸÇÿπŸä */
  animation: truckDriving 2s ease-in-out infinite;
}

/* ÿ™ÿ£ÿ´Ÿäÿ± ÿßŸáÿ™ÿ≤ÿßÿ≤ ÿßŸÑÿπÿ¨ŸÑÿßÿ™ */
@keyframes wheelVibration {
  0% {
    transform: rotate(0deg) translateY(0px);
  }
  25% {
    transform: rotate(90deg) translateY(-1px);
  }
  50% {
    transform: rotate(180deg) translateY(1px);
  }
  75% {
    transform: rotate(270deg) translateY(-2px);
  }
  100% {
    transform: rotate(360deg) translateY(0px);
  }
}

/* ÿ™ÿ£ÿ´Ÿäÿ± ÿØÿÆŸàŸÑ ŸàÿÆÿ±Ÿàÿ¨ ŸÖŸÜ ÿßŸÑŸÖÿ∑ÿ®ÿßÿ™ */
@keyframes roadBumps {
  0% {
    transform: translateY(0px);
  }
  /* ŸÖÿ∑ÿ® ÿµÿ∫Ÿäÿ± */
  15% {
    transform: translateY(-5px);
  }
  20% {
    transform: translateY(8px);
  }
  25% {
    transform: translateY(-3px);
  }
  30% {
    transform: translateY(0px);
  }
  /* ŸÖÿ∑ÿ® ŸÉÿ®Ÿäÿ± */
  65% {
    transform: translateY(-12px);
  }
  70% {
    transform: translateY(15px);
  }
  75% {
    transform: translateY(-8px);
  }
  80% {
    transform: translateY(4px);
  }
  85% {
    transform: translateY(-2px);
  }
  100% {
    transform: translateY(0px);
  }
}

/* ŸÉŸÑÿßÿ≥ ŸÑŸÑÿßŸáÿ™ÿ≤ÿßÿ≤ ÿßŸÑŸÇŸàŸä ÿπŸÜÿØ ÿßŸÑŸÖÿ±Ÿàÿ± ÿπŸÑŸâ ŸÖÿ∑ÿ® */
.truck-bump {
  animation: truckBump 1.5s ease-in-out;
}

/* ÿ™ÿ£ÿ´Ÿäÿ± ŸàÿßŸÇÿπŸä ŸÖÿπ ÿ≠ÿ±ŸÉÿ© ÿ™ŸÇÿØŸÖ */
.truck-moving {
  animation: truckMovingForward 8s ease-in-out infinite;
}
/* üåô ÿßŸÑŸàÿ∂ÿπ ÿßŸÑŸÑŸäŸÑŸä ŸÑÿµŸÅÿ≠ÿ© ÿßŸÑÿ™ÿ≥ÿ¨ŸäŸÑ */
