/* ÿ™ÿ≠ÿ≥ŸäŸÜ ÿµŸàÿ±ÿ© ÿßŸÑÿ¥ÿßÿ≠ŸÜÿ© ŸÅŸä ÿßŸÑŸàÿ∂ÿπ ÿßŸÑŸÑŸäŸÑŸä */
.dark .truck-image {
  filter: drop-shadow(0 10px 20px rgba(0, 0, 0, 0.5)) brightness(0.9);
}

/* ÿ™ÿ≠ÿ≥ŸäŸÜ ÿ£ÿ≤ÿ±ÿßÿ± ÿßŸÑŸÑÿ∫ÿ© ŸÅŸä ÿßŸÑŸàÿ∂ÿπ ÿßŸÑŸÑŸäŸÑŸä */
.dark .language-switch button {
  background: rgba(255, 255, 255, 0.15) !important;
  color: #ffffff !important;
}

.dark .language-switch button:hover {
  background: rgba(255, 255, 255, 0.25) !important;
}

/* ÿßŸÜÿ™ŸÇÿßŸÑ ÿ≥ŸÑÿ≥ ŸÑÿ¨ŸÖŸäÿπ ÿßŸÑÿπŸÜÿßÿµÿ± */
.register-container,
.register-left,
.register-right,
.register-form-container,
.form-group input,
.form-group select,
.btn-primary,
.language-switch button {
  transition: all 0.4s ease-in-out;
}

/* ÿ™ŸÜÿ≥ŸäŸÇÿßÿ™ ÿ£ÿ≤ÿ±ÿßÿ± ÿßŸÑŸÑÿ∫ÿ© ŸàÿßŸÑŸàÿ∂ÿπ ÿßŸÑŸÑŸäŸÑŸä */
.language-switch {
  position: fixed;
  top: 20px;
  right: 20px;
  display: flex;
  gap: 10px;
  z-index: 1000;
}

.language-switch button {
  background: rgba(255, 255, 255, 0.2);
  border: none;
  border-radius: 50%;
  width: 40px;
  height: 40px;
  cursor: pointer;
  font-size: 16px;
  transition: all 0.3s ease;
  backdrop-filter: blur(10px);
  display: flex;
  align-items: center;
  justify-content: center;
}

.language-switch button:hover {
  background: rgba(255, 255, 255, 0.3);
  transform: scale(1.1);
}

/* ÿßŸÑÿ≠ŸÅÿßÿ∏ ÿπŸÑŸâ ÿßŸÑÿ™ÿµŸÖŸäŸÖ ÿßŸÑÿ£ÿµŸÑŸä */
.register-container {
  direction: ltr !important;
}

.register-left,
.register-right,
.register-form-container,
.register-form,
.form-group,
.register-header {
  direction: ltr !important;
  text-align: left !important;
}

/* ÿ®ÿßŸÇŸä ÿßŸÑÿ™ŸÜÿ≥ŸäŸÇÿßÿ™ ÿßŸÑÿ£ÿµŸÑŸäÿ© ÿ™ÿ®ŸÇŸâ ŸÉŸÖÿß ŸáŸä */
