/* src/style/register.css */
.register-container {
  min-height: 100vh;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  display: flex;
  flex-direction: column;
  padding: 20px;
}

/* üåê ÿ£ÿ≤ÿ±ÿßÿ± ÿßŸÑÿ™ÿ≠ŸÉŸÖ */
.control-buttons {
  position: fixed;
  top: 20px;
  right: 20px;
  display: flex;
  gap: 10px;
  z-index: 1000;
}

.control-buttons button {
  background: rgba(255, 255, 255, 0.2);
  border: none;
  padding: 8px 12px;
  border-radius: 20px;
  color: white;
  cursor: pointer;
  font-size: 12px;
  backdrop-filter: blur(10px);
  transition: all 0.3s ease;
}

.control-buttons button:hover {
  background: rgba(255, 255, 255, 0.3);
  transform: translateY(-2px);
}

.test-btn {
  background: #4CAF50 !important;
}

/* üéØ ŸÖÿ≠ÿ™ŸàŸâ ÿßŸÑÿµŸÅÿ≠ÿ© */
.register-content {
  display: flex;
  align-items: center;
  justify-content: center;
  flex: 1;
  gap: 50px;
  max-width: 1200px;
  margin: 0 auto;
  padding: 40px 20px;
}

/* üìù ÿßŸÑÿ¨ÿßŸÜÿ® ÿßŸÑÿ£Ÿäÿ≥ÿ± - ÿßŸÑŸÖÿπŸÑŸàŸÖÿßÿ™ */
.register-info {
  flex: 1;
  color: white;
  text-align: center;
  max-width: 400px;
}

.truck-animation {
  font-size: 80px;
  margin-bottom: 20px;
  display: inline-block;
}

.app-title {
  font-size: 2.5rem;
  font-weight: 800;
  margin-bottom: 15px;
  background: linear-gradient(45deg, #fff, #e3f2fd);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.app-description {
  font-size: 1.1rem;
  opacity: 0.9;
  line-height: 1.6;
  margin-bottom: 30px;
}

.features {
  display: flex;
  flex-direction: column;
  gap: 15px;
}

.feature {
  display: flex;
  align-items: center;
  gap: 15px;
  background: rgba(255, 255, 255, 0.1);
  padding: 15px;
  border-radius: 10px;
  backdrop-filter: blur(10px);
}

.feature span {
  font-size: 24px;
}

.feature p {
  margin: 0;
  font-weight: 500;
}

/* üìã ÿßŸÑÿ¨ÿßŸÜÿ® ÿßŸÑÿ£ŸäŸÖŸÜ - ÿßŸÑŸÜŸÖŸàÿ∞ÿ¨ */
.register-form-section {
  flex: 1;
  max-width: 450px;
}

.form-container {
  background: white;
  padding: 40px;
  border-radius: 20px;
  box-shadow: 0 15px 35px rgba(0, 0, 0, 0.1);
  border: 1px solid #e0e0e0;
}

.form-header {
  text-align: center;
  margin-bottom: 30px;
}

.form-badge {
  display: inline-block;
  background: linear-gradient(45deg, #667eea, #764ba2);
  color: white;
  padding: 8px 16px;
  border-radius: 25px;
  font-size: 0.9rem;
  font-weight: 600;
  margin-bottom: 15px;
}

.form-header h2 {
  font-size: 1.8rem;
  color: #2d3748;
  margin-bottom: 10px;
  font-weight: 700;
}

.form-subtitle {
  color: #718096;
  font-size: 0.95rem;
  line-height: 1.5;
}

/* üìÑ ÿßŸÑŸÜŸÖÿßÿ∞ÿ¨ */
.register-form,
.verification-form {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.form-group {
  display: flex;
  flex-direction: column;
}

.form-group label {
  font-weight: 600;
  margin-bottom: 8px;
  color: #4a5568;
  font-size: 0.95rem;
}

.form-group input,
.form-group select {
  padding: 12px 16px;
  border: 2px solid #e2e8f0;
  border-radius: 10px;
  font-size: 1rem;
  transition: all 0.3s ease;
  background: #f8fafc;
  font-family: inherit;
}

.form-group input:focus,
.form-group select:focus {
  outline: none;
  border-color: #667eea;
  background: white;
  box-shadow: 0 0 0 3px rgba(102, 126, 234, 0.1);
}

.form-group input:disabled,
.form-group select:disabled {
  opacity: 0.6;
  cursor: not-allowed;
}

.form-group input::placeholder {
  color: #a0aec0;
}

.password-hint,
.code-hint {
  color: #718096;
  font-size: 0.8rem;
  margin-top: 5px;
}

.code-hint strong {
  color: #667eea;
}

/* üéØ ÿßŸÑÿ£ÿ≤ÿ±ÿßÿ± */
.submit-btn,
.verify-btn {
  padding: 14px;
  background: linear-gradient(45deg, #667eea, #764ba2);
  color: white;
  border: none;
  border-radius: 10px;
  font-size: 1.1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  margin-top: 10px;
  font-family: inherit;
}

.submit-btn:hover:not(:disabled),
.verify-btn:hover:not(:disabled) {
  transform: translateY(-2px);
  box-shadow: 0 8px 20px rgba(102, 126, 234, 0.3);
}

.submit-btn:disabled,
.verify-btn:disabled {
  opacity: 0.6;
  cursor: not-allowed;
  transform: none !important;
}

.submit-btn.loading,
.verify-btn.loading {
  background: #a0aec0 !important;
}

.back-btn {
  background: none;
  border: 2px solid #e2e8f0;
  color: #718096;
  padding: 12px;
  border-radius: 10px;
  cursor: pointer;
  font-size: 0.95rem;
  transition: all 0.3s ease;
  margin-top: 10px;
}

.back-btn:hover:not(:disabled) {
  border-color: #667eea;
  color: #667eea;
}

/* üí¨ ÿßŸÑÿ±ÿ≥ÿßÿ¶ŸÑ */
.message {
  padding: 12px 16px;
  border-radius: 10px;
  font-weight: 500;
  text-align: center;
  margin-top: 15px;
}

.message.success {
  background: #f0fff4;
  color: #2d7d32;
  border: 1px solid #9ae6b4;
}

.message.error {
  background: #fed7d7;
  color: #c53030;
  border: 1px solid #feb2b2;
}

/* üîó ÿßŸÑÿ±Ÿàÿßÿ®ÿ∑ */
.auth-links {
  text-align: center;
  margin-top: 20px;
  padding-top: 20px;
  border-top: 1px solid #e2e8f0;
}

.auth-links p {
  color: #718096;
  font-size: 0.95rem;
}

.login-link {
  color: #667eea;
  font-weight: 600;
  text-decoration: none;
  transition: color 0.3s ease;
}

.login-link:hover {
  color: #764ba2;
  text-decoration: underline;
}

/* üåô ÿßŸÑŸàÿ∂ÿπ ÿßŸÑŸÑŸäŸÑŸä */
.register-container.dark {
  background: linear-gradient(135deg, #1e3c72 0%, #2a5298 100%);
}

.register-container.dark .form-container {
  background: #2d2d2d;
  border-color: #404040;
  box-shadow: 0 15px 35px rgba(0, 0, 0, 0.4);
}

.register-container.dark .form-header h2 {
  color: #ffffff;
}

.register-container.dark .form-subtitle {
  color: #b0b0b0;
}

.register-container.dark .form-group label {
  color: #e0e0e0;
}

.register-container.dark .form-group input,
.register-container.dark .form-group select {
  background: #3a3a3a;
  border-color: #555;
  color: #ffffff;
}

.register-container.dark .form-group input:focus,
.register-container.dark .form-group select:focus {
  background: #444;
  border-color: #667eea;
}

.register-container.dark .form-group input::placeholder {
  color: #888;
}

.register-container.dark .password-hint,
.register-container.dark .code-hint {
  color: #b0b0b0;
}

.register-container.dark .auth-links {
  border-color: #555;
}

.register-container.dark .auth-links p {
  color: #b0b0b0;
}

/* üì± ÿßŸÑÿ™ÿµŸÖŸäŸÖ ÿßŸÑŸÖÿ™ÿ¨ÿßŸàÿ® */
@media (max-width: 768px) {
  .register-content {
    flex-direction: column;
    gap: 30px;
    padding: 20px 10px;
  }
  
  .register-info {
    max-width: 100%;
  }
  
  .form-container {
    padding: 30px 20px;
  }
  
  .control-buttons {
    position: static;
    justify-content: center;
    margin-bottom: 20px;
  }
  
  .features {
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
  }
  
  .feature {
    flex: 1;
    min-width: 150px;
    flex-direction: column;
    text-align: center;
  }
}